// Base styles

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
    color: $primary-color;
    background-color: $white-color;
    font-family: $primary-font;
    font-weight: $font-regular;
    line-height: 1.45;
    min-width: 320px;
    overflow-x: hidden;
	margin: 0;
}

a {
    display: inline-block;
    text-decoration: none;
}

img {display: block;};
p {margin: 0;}
h1,h2,h3 {margin: 0;}
ul {margin: 0; padding: 0;}
li {list-style: none;}

.container {
    max-width: 1210px;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
}

.slick-arrow {
	width: 55px;
	height: 55px;
	border: 2px solid $white-color;
	background-color: transparent;
	outline: none;
	font-size: 0;
    cursor: pointer;
    
    background-position: center;
    background-repeat: no-repeat;
    z-index: 10;
}

.slick-prev {
    background-image: url('../images/index/icons/arrow-left.svg');
}

.slick-next {
    background-image: url('../images/index/icons/arrow-right.svg');
}

