/* STYLES FOR SECTIONS */

.section {
    padding: 100px 0;
    overflow-x: hidden;
    overflow-y: hidden;
    background-color: $white-color;
    @include media((
        padding: (phoneWide: 70px 0, phone: 50px 0, smallPhone: 40px 0)
    ));
}

.section-content-holder {
    display: flex;
    justify-content: center;

    max-width: 1000px;
    width: 100%;
    margin: 0 auto;
}

.section-text-side-holder {
    display: flex;
    flex-direction: column;
    max-width: 336px;
    width: 100%;
    margin-top: 30px;
    @include media((
        margin-top: (tabletSmall: 25px, phablet: 20px, phoneWide: 15px, phone: 0px),
    ));
}

.section-images-side-holder {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.section-img {
    display: block;
    max-width: 450px;
    width: 100%;
}

.section-title {
    color: $primary-color;
    font-family: $secondary-font;
    font-size: 50px;
    font-weight: $font-regular;
    letter-spacing: 0.2em;
    line-height: 1.25;
    @include media((
        font-size: (phablet: 35px, phoneWide: 30px, phone: 25px),
    ));
}

.section-title-small {
    color: #985e5b;
    font-family: $primary-font;
    font-size: 12px;
    font-weight: $font-bold;
    text-transform: uppercase;
    letter-spacing: 1.2px;
}

.section-text {
    color: $paragraph-color;
    font-family: $primary-font;
    font-size: 12px;
    font-weight: $font-medium;
    line-height: 30px;
    letter-spacing: 1.8px;
    margin-top: 13px;
    
}
